 *{
     padding: 0;
     margin: 0;
     box-sizing: border-box;
 }
.todo{
    display: flex;
   justify-content: space-between;
    background-color: aqua;
    height: 100%;
    width: 100%;  
    margin: 5px 10px;
   
}
.todo button{
    height: 25px;
    padding: 5px;
    margin: 4px 4px;
    border-radius: 25%;
    border: none;
}
 .todo h3{
     margin-left: 15px; 
 }