.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Wrapper{
  background:#282c34;
  color: rgb(243, 230, 230) 
}
.Input-wrapper{
  display: flex;
  justify-content: space-between;
  width: 200px;

}

.Input-wrapper Input{
  border-radius: 4%;
  border: 0;
  background-color: white;
  height: 35px;
}
.Input-wrapper button{
  border-radius: 25%;
    border: none;
    width: 15px;
    align-items: center;
}
